const uniShowModel = (title, content, cancenText = "", confirmText = "", 
showCancel = true, callfunc) => {
	uni.showModal({
		title: title,
		content: content,
		cancelText: cancenText,
		confirmText: confirmText,
		confirmColor: "#f55850",
		cancelColor: "#39B54A",
		showCancel: showCancel,
		success(res){
			console.log("request.js uniShowModel res: " + JSON.stringify(res));
			if (callfunc) {
				callfunc(res.confirm);
			}
			/*if (res.confirm) {
			}
			else {
			}*/
		}
	})
}

const uniShowToast = (title, icon, duration = 3000) => {
	if (icon == null || icon == "") {
		uni.showToast({
			title: title,
			duration: duration
		});
	}
	else {
		uni.showToast({
			title: title,
			icon: icon,
			duration: duration
		});
	}
}

//
// uniPreviewImage : 
//
const uniPreviewImage = (imgs, index) => {
	uni.previewImage({
		urls: imgs,
		current: index
	});
}

//
// uniChoseImage : 
//
const uniChoseImage = (count, callfunc) => {
	uni.chooseImage({
		count: count,
		success(res) {
			if (callfunc) {
				callfunc(0, res);
			}
		},
		fail(err) {
			if (callfunc) {
				callfunc(1, err);
			}
		}
	})
}

//
// getWeixinOepnInfo : openIdsession_key
//
const getWeixinOepnInfo = (appId, secret, accessCode, callfunc) => {
	uni.request({
		url: "https://api.weixin.qq.com/sns/jscode2session",
		method: "GET",
		data: {
			appid: appId,
			secret: secret,
			js_code: accessCode,
			grant_type: "authorization_code"
		},
		success(res) {
			console.log("request.js getWeixinOepnInfo success, res:" + JSON.stringify(res));
			if (callfunc) {
				callfunc(error_code_ok, res);
			}
		},
		fail(err) {
			console.log("request.js getWeixinOepnInfo fail, err:" + JSON.stringify(err));
			if (callfunc) {
				callfunc(error_code_fail, err);
			}
		}
	});
}

