/**
 * json
 *  1. null 
           2. 
 * @param str  
 * @returns Boolean
 */
const isJsonString = str => { // json
    try {
        const toObj = JSON.parse(str);
        if (toObj && typeof toObj === 'object') {
            return true
        }
    } catch {}
    return false
}

export default {
    set(name, value) {
        if (value && typeof value == 'object') { //json
            uni.setStorageSync(name, JSON.stringify(value));
        } 
		else { //
            uni.setStorageSync(name, value);
        }
    },
	
    get(name) { //
        var data = uni.getStorageSync(name);
        if (data) {
            if (isJsonString(data)) { //json
                return JSON.parse(data);
            }
            return data;
        }
        return null;
    },
	
    remove(name) { // 
        uni.removeStorageSync(name);
    },
	
    clear() { // 
        uni.clearStorageSync();
    },
	
	//
	// getUserInfo : 
	//
	getUserInfo() {
		let That = this;
		return That.get("userInfo");
	},
	
	//
	// setUserInfo : 
	//
	setUserInfo(data) {
		let That = this;
		That.set("userInfo", data);
	},
	
	//
	// setArticleData : 
	//
	setArticleData(data) {
		let That = this;
		That.set("articleData", data);
	},
	
	//
	// getArticleData : 
	//
	getArticleData() {
		let That = this;
		return That.get("articleData");
	},
	
	//
	// setCropperImageData : 
	//
	setCropperImageData(data) {
		let That = this;
		That.set("cropperImage", data);
	},
	
	//
	// getCropperImageData : 
	//
	getCropperImageData() {
		let That = this;
		return That.get("cropperImage");
	},
	
	removeCropperImageData() {
		let That = this;
		That.remove("cropperImage");
	}
	
}